import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { NgFlashMessageService } from 'ng-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
 // email: String;
  password: String;

  constructor(
    private validateService: ValidateService, 
    private flashMessage: NgFlashMessageService, 
    private authService: AuthService,
    private router: Router
    ) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const newu = {
      name: this.name,
      username: this.username,
     // email: this.email,
      password: this.password
    }

  // Required Fields
    if(!this.validateService.validateRegister(newu)) {
      this.flashMessage.showFlashMessage({messages: ['Please fill in all fields'], type: 'danger', timeout:2000});
      return false;
    }

    // Validate Email
    // if(!this.validateService.validateEmail(user.email)) {
    //   this.flashMessage.showFlashMessage({messages: ['Please use a valid email'], type: 'danger', timeout:2000});
    //   return false;
    // }

    // Register User
    this.authService.registerNewu(newu).subscribe(data => {
      if(data.success) {
        this.flashMessage.showFlashMessage({
          messages: ['환영해~~앱에서 확인해보쇼!'], 
          type: 'success', 
          timeout:5000
        });
        this.router.navigate(['/']);
      } else {
        this.flashMessage.showFlashMessage(
          {messages: ['뭔가 잘못됐어 ㅠㅠ'], 
          type: 'danger', 
          timeout:3000
        });
        this.router.navigate(['/register']);
      }
    })
  }

}
